<template>
  <div :class="['auto-center', 'wrap']">
    <a-row :type="'flex'" :justify="'center'">
      <a-col :span="8">
        <a-input
          v-model="inputData"
          @keyup.enter="addItem()"
          v-bind:placeholder="'请输入代办事项'"
        />
      </a-col>
      <a-col :span="2">
        <a-button @click="addItem()" type="primary" icon="plus">添加</a-button>
      </a-col>
    </a-row>
    <a-row :type="'flex'">
      <todo-item
        :itemsProps="items"
        @handleDeleteItemProps="handleDeleteItem"
      />
    </a-row>
  </div>
</template>

<script>
import TodoItem from "./components/index";
import uuid from "uuid";

export default {
  name: "todoList",
  components: {
    TodoItem
  },
  data() {
    return {
      inputData: "",
      items: [
        {
          text: "打球",
          uuid: uuid(),
          checked: false
        }
      ]
    };
  },
  computed: {
    // 计算属性的 getter
    reversedMessage() {
      // `this` 指向 vm 实例
      return Date.now();
    }
  },
  methods: {
    addItem() {
      if (this.inputData === "") {
        return;
      }
      this.items.push({ text: this.inputData, uuid: uuid(), checked: false });
      this.inputData = "";
    },
    handleDeleteItem(uuid) {
      window.console.log("父组件打印的", uuid);
      this.items = this.items.filter(item => item.uuid !== uuid);
    }
  }
};
</script>

<style scoped>
.wrap {
  width: 50%;
  margin: 0 auto;
}
.auto-center {
  margin: 0 auto;
}
</style>
